<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process22"></a>

<p><br></p>
<p><span class="rvts17">费恩（Fane）：</span></p>
<p><img width="401" height="615" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber72.jpg"><span class="rvts18"></span>
<br><span class="rvts21">当你醒来，原来的世界已经毁灭。作为种族的最后一员，费恩把自己隐藏在一具迷人的面具下。然而，他也在试图寻找那段不为人知的历史秘密。</span>
<br><span class="rvts29">种族：不死族</span>
<br><span class="rvts21">默认职业：巫师（</span><span class="rvts20">Wizard</span><span class="rvts21">）</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">费恩拥有不死族的不死天赋，然而，费恩还会拥有人族的心灵手巧天赋。</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">费恩只能被选为男性，并附带费恩特有标签。该标签可以使得他拥有一些特定的对话和行动。另外他还具备贵族和野蛮人的标签。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="140" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="779" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber73.jpg"></p>
</td>
<td width="140" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Play Dead</span></p>
</td>
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">装死</span></p>
</td>
<td width="779" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">立即进入装死状态，降低仇恨值，持续</span><span class="rvts20">3</span><span class="rvts21">轮，</span>
<br><span class="rvts21">但所有的技能冷却也会暂停，</span>
<br><span class="rvts21">任何行动会取消装死，该技能不会影响角色的潜行和隐形状态</span></p>
</td>
</tr>
<tr valign="top">
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber74.jpg"></p>
</td>
<td width="140" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Time Warp</span></p>
</td>
<td width="116" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">时光隧道</span></p>
</td>
<td width="779" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">使目标获得</span><span class="rvts20">1</span><span class="rvts21">次额外的行动机会</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">费恩拥有不死族的天赋，因此会受到普通治疗带来的伤害，但同事他也会从中毒效果中获益。</span>
<br><span class="rvts20">2. </span><span class="rvts21">费恩的手指具备独特的开锁功能，利用这点，可以很好的进行大门以及箱子的开锁活动。</span>
<br><span class="rvts20">3. </span><span class="rvts21">在费恩逃脱欢乐堡之后，他将修复变形者的面具。之后，利用该道具，可以使它变为精灵，进行食尸（鲜血献祭），获得额外的记忆和更多免费的技能，详见种族篇中精灵族的部分内容。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



